<template>
   <div class="BxBookCoach__Container__BxCoachAvailable">
      <div class="BxBookCoach__Container__BxCoachAvailable__title">
         <p>Available Coach</p>
      </div>
      <div class="BxBookCoach__Container__BxCoachAvailable__view">
         <div v-if="showLottie" class="NocoachAvailable">
            <BlueLoader  :height="200" :width="200" />
         </div>
         <div v-if="ShowCoachfilter.message === 'Coach not found' && !showLottie" class="NocoachAvailable">
            <div class="NoAvailable">
               <p>No Coaches</p>
               <p>Available</p>
            </div>
         </div>
         <div class="BXcoachProf"
            v-for="(n, index) in CoachResult.coach_result"
            :class="{ 'active': coachActiveSelect.CoachIndex === index}"
            :key="index"
            @click="setActive(index, n.name, n.id, n.avatar, n.coach_rating, n.coach_cost)"
            v-if="ShowCoachfilter.message !== 'Coach not found' && !showLottie"
         >
            <div class="CoachProfile" >
               <div class="CoachProfile__avatar">
                  <v-avatar size=68>
                     <img
                     :src="n.avatar"
                     alt="avatar"
                     >
                  </v-avatar>
               </div>
               <div class="CoachProfile__name text-capitalize">
                  {{n.name}}
               </div>
               <div class="CoachProfile__rating">
                  <v-rating half-increments :value = Number(n.coach_rating) type="number" color="yellow darken-3" readonly small></v-rating>
               </div>
               <div class="CoachProfile__tokens">
                  {{n.coach_cost}} Tokens
               </div>
               <div class="CoachProfile__spokeen">
                  <p>Spoken Language</p>
                  <p class="text-capitalize">{{n.spoken_language}}</p>
               </div>
               <div class="CoachProfile__country">
                  <p>Country</p>
                  <p class="text-capitalize">{{n.country}}</p>
               </div>
            </div>
            <div class="scheduleinfo" @click="SeeProfile(n.id)">
               <router-link to="/student/view" target="_blank">See Profile</router-link>
            </div>
         </div>
         <div class="BXcoachProf" style="visibility: hidden;height:1px"></div>
         <div class="BXcoachProf" style="visibility: hidden;height:1px"></div>
         <div class="BXcoachProf" style="visibility: hidden;height:1px"></div>
         <div class="BXcoachProf" style="visibility: hidden;height:1px"></div>
         <div class="BXcoachProf" style="visibility: hidden;height:1px"></div>
        
      </div>
   </div>
</template>

<script>
   import BlueLoader from '../../../views/Lottie/LittleLoading'
   export default {
      components: {
         BlueLoader
      },
      props: [
         'GET_CoachResult',
         'GET_CoachSelect',
         'lottetest'
      ],
      data () {
         return {
            rating: '3',
            //FOR LOTTIE
            showLottie:false
         }
      },
      methods: {
         setActive(index, name, id, avatar, coach_rating, coach_cost) {
            var CR = Number(coach_rating)
         
            this.$store.commit('availableCoachDataSelect',{
               CoachName : name,
               CoachID : id,
               CoachIndex : index,
               CoachAvatar : avatar,
               Coach_rating : CR,
               CoachCost: coach_cost
            })
            this.$store.commit('storeCoachTimeAvailable',{
               coach_schedule: [],
            })
            const dataCoachDaysOff = {
               username : id
            }
            this.$store.dispatch('POST_CHECK_DAYSOFF', dataCoachDaysOff);

         },
   
         SeeProfile(id){
            this.$store.commit('muttUpcomingSelect',{
                    Username : id
            })
         }
      },
      computed: {
         CoachResult(){
            return this.GET_CoachResult
         },
         coachActiveSelect(){
            return this.GET_CoachSelect
         },
         ShowCoachfilter(){
            return this.$store.getters.IS_COACH
         },
         cekFilterLoading(){
            return this.$store.state.FilterLoading
         },
         ChkLoadFilter(){
            return this.$store.state.loadFilter
         }
        
      },
      watch: {
         cekFilterLoading(val){
            if (val === true){
               this.showLottie = true
            }
         },
         ChkLoadFilter(val){
            if (!val)
               return false

            if (val.status === 200 || val.status === 404){
               setTimeout(() => {
                  this.showLottie = false
                  this.$store.commit('storeFilterLoading',false)
               }, 1000);
            }
         }
      },


   }
</script>



