<template>
	<div class="BxBookCoach__Container__BxFilter">
		<div class="SelecBy">
			<v-select
			:items="selectitemBy"
			v-model="selectBy"
			label="Search By"
			solo
      	></v-select>
		</div>
		<div class="FilterByDate" v-if="selectBy == 'Date'">
			<div class="byBookType">
				<v-select
				:items="bookTypeItems"
				v-model="bookType"
				label="Book Type"
				solo
				></v-select>
			</div>

			<div class="ByWeek" v-if="bookType === 'Recurring'">
				<v-select
				:items="byWeeksItems"
				v-model="byWeeks"
				label="Select Week"
				solo
				></v-select>
			</div>

			<!-- FILTER BY DATE -->
			<div class="byDate" v-if="bookType === 'Single'">
				<v-dialog
					ref="dialog"
					v-model="modal"
					:return-value.sync="date"
					persistent
					lazy
					full-width
					width="290px"
					content-class="DialogDate"
					
				>
					<v-text-field
						slot="activator"
						:value="computedDateFormattedMomentjs"
						readonly
						solo
						label="Select Date"
					></v-text-field>
					<v-date-picker v-model="date"   scrollable color="#0F77AC">
						<v-spacer></v-spacer>
						<v-btn flat color="primary" @click="modal = false">Cancel</v-btn>
						<v-btn flat color="primary" @click="$refs.dialog.save(date); POST_FilterByDateSingle()" :disabled="!date">OK</v-btn>
					</v-date-picker>
				</v-dialog>
			</div>

		</div>

		<!-- FILTER BY COUNTRY -->
		<div class="FilterByCountry" v-if="selectBy == 'Country'">
			<v-select
				:items="listofcountry"
				v-model="country"
				label="Select Country"
				item-text="country"
				hide-details solo full-width
				@change ="ShowBtnCountry"
			>
			</v-select>

			<div class="btnFilter">
				<v-btn
					color="#0F77AC"
					@click="POST_FilterByCountry"
					v-if="showbtnCountry"
				>
        			<v-icon small color="#fff">search</v-icon>
      		</v-btn>
			</div>
		</div>

		<!-- FILTER BY SPOKEN LANGUAGE -->
		<div class="FilterBySpoken" v-if="selectBy == 'Language'">
			<v-select
				label="Coach Language"
				:items="listoflanguage"
				v-model="homeLanguage"
				item-text="spoken_language"
				persistent-hint
				solo
				:menu-props="{ maxHeight: '300' }"
				@change="ShowBtnLanguage"
			></v-select>

			<div class="btnFilter">
				<v-btn
					color="#0F77AC"
					@click="POST_FilterByLanguage"
					v-if="showbtnLanguage"
				>
        			<v-icon small color="#fff">search</v-icon>
      		</v-btn>
			</div>
		</div>

		<!-- FILTER BY NAME -->
		<div class="FilterByName" v-if="selectBy == 'Name'">
			<v-text-field required v-model='name' placeholder="Coach Name" solo></v-text-field>
			<div class="btnFilter" v-if="name !==''">
				<v-btn
					color="#0F77AC"
					@click="POST_FilterByName"
				>
        			<v-icon small color="#fff">search</v-icon>
      		</v-btn>
			</div>
		</div>
	</div>
</template>

<script>
	import moment from 'moment'
	
	export default {
		components: {

		},
		data () {
			return {
				//For Filter Select By
				selectitemBy :['Country', 'Date', 'Name', 'Language'],
				byWeeksItems: ['2 Weeks','3 Weeks','4 Weeks'],
				modal: false,
				selectBy: '',
				//For Filter SpokenLanguage
				homeLanguage:'',
				listoflanguage :'',
				showbtnLanguage:false,
				//For Filter Country
				country: '',
				listofcountry:'',
				showbtnCountry:false,
				//For Filter name
				name: '',
				//For Filter Date singel
				date: null,
				loadingDateSingle: null,
				loadingDateReccuring:null,
				mindate:new Date().toISOString().substr(0, 10) + 1,

				//For Filter Date Reccuring
				dateReccuring: null,
				loadingDateSingle: null,
				modalReccuring: false,


				//For Filter book type
				bookType:'',
				bookTypeItems :['Single','Recurring'],
				byWeeks:'',
			}
		},
		computed: {
    		computedDateFormattedMomentjs () {
      		return this.date ? moment(this.date).format('MMMM Do  -  YYYY') : ''
			},
			FormateDate2 () {
      		return this.dateReccuring ? moment(this.dateReccuring).format('MMMM Do  -  YYYY') : ''
			},
			GetSelect () {
				return this.$store.state.AvailableSelect
			},
			GetFormatDay() {
            return this.date ? moment(this.date).format('dddd') : ''
			},
		},
		methods: {
			POST_FilterByCountry() {
				const FilterCountry = this.country
				this.$store.dispatch('POST_FILTERCOUNTRY', FilterCountry);
				this.$store.commit('storeFilterLoading',true)
			},
			ShowBtnCountry() {
				this.showbtnCountry = true
			},
			POST_FilterByLanguage() {
				const FilterLanguage = this.homeLanguage
				this.$store.dispatch('POST_FILTERLANGUAGE', FilterLanguage)
				this.$store.commit('storeFilterLoading',true)
			},
			ShowBtnLanguage() {
				this.showbtnLanguage = true
			},
			POST_FilterByName() {
				const FilterName = this.name
				this.$store.commit('storeFilterLoading',true)
				this.$store.dispatch('POST_FILTERNAME', FilterName)
			},
			POST_FilterByDateSingle() {
				const FilterDate = this.date
				this.$store.dispatch('POST_FILTERDATE', FilterDate)
				this.$store.commit('storeFilterLoading',true)
			},		

		},
		created() {
			this.listoflanguage = this.GetSelect.input_list
			this.listofcountry = this.GetSelect.input_list
		},
		watch:{
			selectBy (val){
				if (val === 'Date'){
					this.$store.commit('storeAvailableFilter', val)
				}else{
					this.$store.commit('storeAvailableFilter', null)
				}
				this.$store.commit('storeCoachTimeAvailable',{
               	coach_schedule: [],
				})
				this.$store.commit('storeDataAvailableCoach',{
               	message: "Coach not found",
				})
				this.$store.commit('availableCoachDataSelect',{
						CoachIndex : 'undefined'
				})
				this.$store.commit('muttloadFilter', null)
			},
			bookType(val){
				if(val === 'Recurring'){
					this.$store.commit('StoreFilterBookType', {
						bookType: 'Recurring',
						booking_type: 'multiple',
						frequency: null
					})
				}else{
					this.$store.commit('StoreFilterBookType', {
						bookType: 'Single',
						booking_type: null,
						frequency: null
					})
				}
				this.$store.commit('storeCoachTimeAvailable',{
               	coach_schedule: [],
				})
				this.$store.commit('availableCoachDataSelect',{
						CoachIndex : 'undefined'
				})
				this.$store.commit('storeDataAvailableCoach',{
               	message: "Coach not found",
				})
			
			},
			byWeeks(val){
				this.$store.commit('storeCoachTimeAvailable',{
               	coach_schedule: [],
				})
				if (val === '2 Weeks'){
					const ReccuringData = {
						selector: 'recurring',
						frequency: 2
					}
					this.$store.commit('storeFilterLoading',true)
					this.$store.dispatch('POST_FILTER_RECCURING', ReccuringData);
					this.$store.commit('StoreFilterBookType', {
						bookType: 'Recurring',
						booking_type: 'multiple',
						frequency: 2
					})
					
					
				}else if (val === '3 Weeks') {
					const ReccuringData = {
						selector: 'recurring',
						frequency: 3
					}
					this.$store.commit('storeFilterLoading',true)
					this.$store.dispatch('POST_FILTER_RECCURING', ReccuringData);
					this.$store.commit('StoreFilterBookType', {
						bookType: 'Recurring',
						booking_type: 'multiple',
						frequency: 3
					})

				}else if (val === '4 Weeks') {
					const ReccuringData = {
						selector: 'recurring',
						frequency: 4
					}
					this.$store.commit('storeFilterLoading',true)
					this.$store.dispatch('POST_FILTER_RECCURING', ReccuringData);
					this.$store.commit('StoreFilterBookType', {
						bookType: 'Recurring',
						booking_type: 'multiple',
						frequency: 4
					})
				
				}

				

			},
			date (val) {
				if (!val) return
					this.$store.commit('storeFilterGetData',{
						TheDate : this.date,
						day :  this.GetFormatDay
					})
				
			}
		}
	}
</script>



