<template>
   <div class="BxBookCoach__CoachDetail" id="CoachDetail">
      <div class="boxNotif">
         <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top 
			   >
				<p>{{NotifText}}</p>
				<!-- <v-btn dark flat @click="snackbar = false">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>
      <div id="CoachDetail1" v-if="coachActiveSelect.CoachIndex !== 'undefined'">
         <div class="BxBookCoach__CoachDetail__CoachBio">
            <div class="avatar">
               <v-avatar size=70>
                  <img :src="coachActiveSelect.CoachAvatar" alt="Avatar">
               </v-avatar>
            </div>
            <div class="sideBio">
               <p class="name">{{coachActiveSelect.CoachName}}</p>
               <div class="rating">
                  <v-rating half-increments v-model="coachActiveSelect.Coach_rating" color="yellow darken-3" small readonly></v-rating>
               </div>
               <p class="tokens">{{coachActiveSelect.CoachCost}} Tokens Coach</p>
            </div>
         </div>
         <div>

         </div>
         <div class="BxBookCoach__CoachDetail__calendar">
            <v-date-picker
               v-model="date"
                :min="mindate"
               event-color="red lighten-1"
               :events="arrayEvents"
               :title-date-format="formatDate"
               no-title
            >
            </v-date-picker>
         </div>
         <div class="BxBookCoach__CoachDetail__time">
            <div class="headTime" id="headTime">
               <p>Available Time</p>
               <p>choose your time wisely, but also can reschedule it 24 hours before it starts.</p>
            </div>
            <div class="timepicker" >
               <div class="bxEvent" id="bxEvent">
                  <div v-if="showLottie" style="margin-top:25px;">
                      <LittleLoader style="height:100px;width:120px"/>
                  </div>
                  <div class="events" v-if="ShowedCoachScheduleTime === 'Schedule not found' && !showLottie">
                     <h4>No Available Coach Schedule </h4>
                     <h4 v-if="date !== null">{{theDateComputed | date}}</h4>
                  </div>
                  <div class="events" v-if="ShowedCoachScheduleTime === 'Coach is having dayoff' && !showLottie" style="margin-top:30px;">
                     <h4>Coach Have Day off </h4>
                     <h4 v-if="date !== null">{{theDateComputed | date}}</h4>
                  </div>
                  <div class="bxDate" v-if="ShowedCoachScheduleTime !== 'Schedule not found' && !showLottie">
                     <span
                        @click="current = index"
                        :class="{spanIsActive:index == current}"
                        v-for="(item, index) in CoachCheckSheduleTime.coach_schedule"
                        :key="index"
                     >
                        {{item}}
                     </span>
                     <span style="visibility: hidden"></span>
                     <span style="visibility: hidden"></span>

                  </div>
               </div>
            </div>
         </div>
         <div class="BtnBook" v-if="current !== null" id="BtnBook">
            <v-btn :disabled="dialogLoding" @click="dialogLoding = true">Book Coach</v-btn>
         </div>
         <v-dialog v-model="dialogLoding" hide-overlay width="100" persistent content-class="test">
            <v-card class="fpassword--loder">
               <v-card-text class="text-xs-center">
                  <v-progress-circular
                     indeterminate
                     color="primary"
                     :size="50"
                     :width="7"
                  ></v-progress-circular>
               </v-card-text>
            </v-card>
         </v-dialog>
         <v-dialog v-model="dialogWaitStatusBook" content-class="DialogLoder" width="300" persistent style="display:flex;justify-content:center;">
            <v-card class="fpassword--loder">
               <v-card-text class="text-xs-center">
                  <LittleLoader  style="height:250px;width:250px;overflow:hidden;"/>
               </v-card-text>
            </v-card>
         </v-dialog>
         <v-dialog v-model="DbookSummary" width="350" content-class="DialogBookSummary2" persistent>
            <v-card style="border-radius: 3px;overflow-y:hidden;">
               <v-icon @click.native="DbookSummary = false">close</v-icon>
               <div class="ContainerBooksummary" v-if="CheckDataSummary">
                  <div class="bxsummary">
                     <div class="bxHeadSummary">
                        <p class="bxTitle">Book Summary</p>

                     </div>
                     <div class="bxSummaryText">
                        <div class="boxSummary">
                           <div class="bxlabels">
                              <p class="labels">Coach Name</p>
                              <span>:&nbsp;</span>
                           </div>
                           <div class="bxAsw">
                              <p class="Answ text-capitalize">{{ShowedDataSummary.summary.coach_name}}</p>
                           </div>
                        </div>
                        <div class="boxSummary">
                           <div class="bxlabels">
                              <p class="labels">Date</p>
                              <span>:&nbsp;</span>
                           </div>
                           <div class="bxAsw">
                              <p class="Answ">{{DateFormattedBookSummary}}</p>
                           </div>
                        </div>
                        <div class="boxSummary">
                           <div class="bxlabels">
                              <p class="labels">Start Time</p>
                              <span>:&nbsp;</span>
                           </div>
                           <div class="bxAsw">
                               <p class="Answ">{{ShowedDataSummary.summary.start_time}}</p>
                           </div>
                        </div>
                        <div class="boxSummary">
                           <div class="bxlabels">
                              <p class="labels">End Time</p>
                              <span>:&nbsp;</span>
                           </div>
                           <div class="bxAsw">
                              <p class="Answ">{{ShowedDataSummary.summary.end_time}}</p>
                           </div>
                        </div>
                        <div class="boxSummary">
                           <div class="bxlabels">
                              <p class="labels">Token Cost</p>
                              <span>:&nbsp;</span>
                           </div>
                           <div class="bxAsw">
                              <p class="Answ">{{ShowedDataSummary.summary.coach_cost}}</p>
                           </div>
                        </div>
                        <div class="boxSummary">
                           <div class="bxlabels">
                                 <p class="labels">Device/Browser</p>
                                 <span>:&nbsp;</span>
                           </div>
                           <div class="bxAsw">
                              <p class="Answ">Dekstop/Chrome</p>
                           </div>
                        </div>
                     </div>
                     <div class="bxButton">
                       
                        <v-btn class="btn1 btnok" @click="POST_ConfirmSummary">
                           Confirm
                        </v-btn>
                        <!-- <v-btn class="btn1 btnno" @click.native="DbookSummary = false">
                           Cancel
                        </v-btn> -->
                     </div>
                  </div>
               </div>
               <Footer width="100%" height="100px" style="margin-bottom:-10px" />

            </v-card>
         </v-dialog>
      </div>
      <div id="CoachDetail2"  v-if="coachActiveSelect.CoachIndex == 'undefined'">
         <p>Choose a coach to</p>
         <p>See its content</p>
      </div>
   </div>
</template>

<script>
   import moment from 'moment'
   import LittleLoader from '../../../views/Lottie/LittleLoading'
   import Footer from '../../../components/icons/Footer'
   import * as easings from 'vuetify/es5/util/easing-patterns'
   export default {
      props :[
         'GET_CoachSelect',
         'LotteLoader'
      ],
       components: {
         LittleLoader,
         Footer
      },
      data () {
         return {
            dialogWaitStatusBook:false,
            dialogLoding: false,
            DbookSummary: false,
            rating: 3,
            current:null,
            mindate:new Date().toISOString().substr(0, 10) + 1,
            date : null,
            time : new Date(),
            event: false,
            arrayEvents: null,
            dateEvent : [],
            snackbar: false,
            NotifColor:'',
            NotifText:'',
            timeout: 5000,
            //FOR LOTTIE
            showLottie:false,
            type: 'selector',
            selector: null,
            duration: 800,
            offset: null,
            easing: 'easeInQuad',
            easings: Object.keys(easings)
         }
      },
      mounted () {
         this.arrayEvents = this.dateEvent

      },
      computed: {
         theDateComputed () {
            const date = new Date(this.date)
            date.setHours(this.time.getHours())
            date.setMinutes(this.time.getMinutes())
            // console.log (date)
            if (this.dateEvent.indexOf(this.date)+1){
               this.event = true
            }else{
               this.event = false
            }
            return date
         },
         //Formate date data
         computedDateFormattedMomentjs () {
            return this.date ? moment(this.date).format('dddd') : ''
         },
         DateFormattedBookSummary() {
            return this.ShowedDataSummary.summary.date ? moment(this.ShowedDataSummary.summary.date).format('MMMM, DD YYYY') : ''
         },
         coachActiveSelect (){
            return this.GET_CoachSelect
         },
         CoachCheckSheduleTime () {
            return this.$store.state.AvailableTimeCoachSelect
         },
         ShowedCoachScheduleTime () {
            return this.$store.getters.IS_COACH_SCHEDULE_TIME.message
         },
         ShowedDataSummary () {
            return this.$store.state.AvailableBookSummary
         },
         CheckDataSummary() {
            return this.$store.getters.IS_BOOK_SUMMARY
         },
         CheckLoadConfirmSummary(){
            if (this.$store.getters.CCK_CONFIRM_BOOK_SUMMARY !== 'IS NULL'){
               const ItemsCont = []
               for (var i in this.$store.getters.CCK_CONFIRM_BOOK_SUMMARY){
                  ItemsCont.push(this.$store.getters.CCK_CONFIRM_BOOK_SUMMARY[i])
               }
               return ItemsCont
            }else{
               return 'IS NULL'
            }
         },
         CheckDaysOff(){
            return this.$store.state.CoachDaysOff ? this.$store.state.CoachDaysOff.coach_dayoff : null
         },
         target () {
            const value = this[this.type]
            return value
         },
         options () {
            return {
               duration: this.duration,
               offset: this.offset,
               easing: this.easing
            }
         },
         MtrLodingSchedule(){
            return this.$store.state.LodingCkSchedule ? this.$store.state.LodingCkSchedule : null
         },
         CheckLoadSummary(){
            return this.$store.state.loadSummary
         }
   
      },
      methods: {
         //Formate date calender header
         formatDate (date) {
            return moment(date).format('ddd, MMMM')
         },
         POST_CHECKSCHEDULE () {
            const CheckSchedule = {
               day :this.computedDateFormattedMomentjs,
               username: this.$store.state.AvailableCoachSelect.CoachID,
               date: this.date
            }
            this.$store.dispatch('POST_CHECK_SCHEDULE_COACH', CheckSchedule);
         },
         POST_BookSummary (){
            const BookSummarydata ={
                day :this.computedDateFormattedMomentjs,
                date: this.date,
                username: this.$store.state.AvailableCoachSelect.CoachID,
                time: this.CoachCheckSheduleTime.coach_schedule[this.current]
            }
            // console.log(BookSummarydata)
            this.$store.dispatch('POST_BOOK_SUMMARY', BookSummarydata)

         },
         POST_ConfirmSummary(){
            this.showLottieLoader()
            const ConfirmBookSummary ={
               username: this.$store.state.AvailableCoachSelect.CoachID,
               date: this.ShowedDataSummary.summary.date,
               time: this.CoachCheckSheduleTime.coach_schedule[this.current],
               // type: 'opentok'
            }
            this.$store.dispatch('POST_CONFIRM_SUMMARY', ConfirmBookSummary)
            this.current = null
            this.dialogWaitStatusBook = true
            this.DbookSummary = false
         },
         
         showLottieLoader () {
            this.POST_CHECKSCHEDULE()
            this.showLottie = true
         },
         DaysOff(){
            const DaysOff = this.$store.state.CoachDaysOff.coach_dayoff 
            const a = []
            this.dateEvent.push(a);

            for (var n in DaysOff) {
               // console.log(DaysOff[n].startTime);
               var startDate = DaysOff[n].datestart;
               var endDate = DaysOff[n].dateend;
               var dateMove = new Date(startDate);
               var strDate = startDate;
               
               while (strDate < endDate){
                  var strDate = dateMove.toISOString().slice(0,10);
                  this.dateEvent.push(strDate);
                  dateMove.setDate(dateMove.getDate()+1);
               };
            }
            
            // console.log(this.dateEvent)
         },
         scrollToEnd() {    	
             this.$vuetify.goTo(this.target, this.options)
             console.log(this.target)
         }
      },
      watch: {
         dialogLoding (val) {
            if (!val) return
            this.POST_BookSummary()
         },
         CheckLoadSummary(val){
            if (val.status === 200 || val.status === 404){
               setTimeout(()=>{
                  this.DbookSummary = true,
                  this.dialogLoding = false
               },1000);
            }
         },
         date(val){
            this.current = null
            this.POST_CHECKSCHEDULE()
            this.showLottieLoader()
            this.selector = "#bxEvent"
            this.offset = 0
            this.scrollToEnd()
         },
         coachActiveSelect(val){
            this.date= null
            this.dateEvent.length= 0
            if(this.coachActiveSelect.CoachIndex !== 'undefined'){
               this.selector = "#CoachDetail"
               this.offset = -70
               this.scrollToEnd()
            }
         },
         CheckLoadConfirmSummary(val){
            const dataConfirmSummary = val[0]
            if(dataConfirmSummary.status === 400 && dataConfirmSummary.data.message === 'not enough token'){
               console.log('Not Enough Token')
               this.NotifColor ='error'
               this.NotifText = 'Not Enough Token'
            }else if(dataConfirmSummary.status === 200 && dataConfirmSummary.data.update_token){
               this.NotifColor ='success'
               this.NotifText = 'Booking Coach is Successfully'
               this.$store.commit('muttShowBadges',true)
               this.POST_CHECKSCHEDULE()
            }else if(dataConfirmSummary.status === 500){
               this.NotifColor ='error'
               this.NotifText = 'Try again in view minutes'
            }
            else if(dataConfirmSummary === undefined){
               this.NotifColor ='error'
               this.NotifText = 'Try again in view minutes'
            }

            setTimeout(() => {
               this.dialogWaitStatusBook = false
               this.snackbar = true
            }, 1000);

            setTimeout(() => {
               this.$store.commit('muttCckConfirmBookSummary', null)
            }, 3000);
         },
         CheckDaysOff(val){
            if (val){
              this.DaysOff()
            }
         },
         MtrLodingSchedule(val){
            if (val.status === 200 || val.status === 404){
                setTimeout(() => {
                   this.showLottie = false
               }, 700);
            }else if (val.status === 500){
               this.NotifColor ='error'
               this.NotifText = 'Please Check Another Date'
               setTimeout(() => {
                  this.snackbar = true
               }, 1000);
            }
         }
         
      },
      filters:{
         count: function(value){
            return value.length;
         }
      }
   }
</script>



