<template>
  <v-app>
     <div v-if="!myData">
        <content-loader 
				:animate="true"
				:height="285"
				:width="600"
				:speed="2"
				primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
            v-if="ResizeScreen == 3"
  		   >
            <rect x="0" y="0" rx="2" ry="2" width="84" height="15" />
            <rect x="0" y="35" rx="2" ry="2" width="60" height="20" />
            <rect x="0" y="60" rx="0" ry="0" width="380" height="285" />
            <rect x="400" y="0" rx="0" ry="0" width="200" height="285" />
         </content-loader>
         <content-loader 
				:animate="true"
				:height="500"
				:width="500"
				:speed="2"
				primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
            v-if="ResizeScreen == 2"
            style="margin-top:135px;"
  		   >
            <rect x="20" y="0" rx="2" ry="2" width="84" height="15" />
            <rect x="20" y="35" rx="2" ry="2" width="100" height="20" />
            <rect x="20" y="60" rx="0" ry="0" width="250" height="500" />
            <rect x="280" y="0" rx="0" ry="0" width="200" height="500" />
         </content-loader>
         <content-loader 
				:animate="true"
				:height="500"
				:width="350"
				:speed="2"
				primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
            v-if="ResizeScreen == 1"
            style="margin-top:80px;"
  		   >
            <rect x="20" y="0" rx="2" ry="2" width="84" height="15" />
            <rect x="20" y="35" rx="2" ry="2" width="100" height="20" />
            <rect x="20" y="60" rx="0" ry="0" width="310" height="230" />
            <rect x="20" y="300" rx="0" ry="0" width="310" height="200" />
            
         </content-loader>
     </div>
     
     <div class="BxBookCoach" v-else>
        <div class="BxBookCoach__Container">
           <div class="BxBookCoach__Container__BxHeader">
              <h3>Book A Coach</h3>
              <div class="blue__circle"></div>
           </div>
           <BookType/>
           <ViewCoach
              v-if="LOAD_ViewCoach && CheckFilter !== 'Date'"
              :GET_CoachResult="GET_CoachResult"
              :GET_CoachSelect="GET_CoachSelect"
           />
           <ViewCoachFilterByDate
              v-if="LOAD_ViewCoach && CheckFilter === 'Date' && CheckBookType.bookType !== 'Recurring'"
              :GET_CoachResult="GET_CoachResult"
              :GET_CoachSelect="GET_CoachSelect"
           />
           <ViewCoachReccuring
               v-if="LOAD_ViewCoach && CheckFilter === 'Date' &&  CheckBookType.bookType === 'Recurring'"
               :GET_CoachResult="GET_CoachResult"
               :GET_CoachSelect="GET_CoachSelect"
           />

        </div>
        <CoachDetail
           :GET_CoachSelect="GET_CoachSelect"
           v-if="CheckFilter !== 'Date'"
        />
        <CoachDetailFilterByDate
           :GET_CoachSelect="GET_CoachSelect"
            v-if="CheckFilter === 'Date' && CheckBookType.bookType !== 'Recurring'"
        />
        <CoachDetailReccuring
            :GET_CoachSelect="GET_CoachSelect"
             v-if="CheckFilter === 'Date' && CheckBookType.bookType === 'Recurring'"
        />
     </div>
  </v-app>
</template>

<script>
	import BookType from '../../student-components/bookcoach/Filter/Filter'
	import ViewCoach from '../../student-components/bookcoach/ViewCoach/ViewCoach'
   import CoachDetail from '../../student-components/bookcoach/coachdetail/CoachDetail'
   import CoachDetailFilterByDate from '../../student-components/bookcoach/coachdetail/CoachDetailFilterDate.vue'
   import CoachDetailReccuring from '../../student-components/bookcoach/coachdetail/CoachDetailByReccuring.vue'
   import ViewCoachFilterByDate from '../../student-components/bookcoach/ViewCoach/ViewCoachFilterByDate.vue'
   import ViewCoachReccuring from '../../student-components/bookcoach/ViewCoach/ViewCoachByReccuring.vue'
	import { ContentLoader } from "vue-content-loader"
	import moment from 'moment'

	export default {
    components: {
      BookType,
      ViewCoach,
      CoachDetail,
      ContentLoader,
      CoachDetailFilterByDate,
      ViewCoachFilterByDate,
      CoachDetailReccuring,
      ViewCoachReccuring
   },
   data () {
      return {
         myData :null,
         windowWidth:0,
         windowHeigt:0,
         ResizeScreen: null
      }
   },
   created() {
      this.TrigDispatch()
      this.getWindowHeight()
      this.getWindowWidth()
   },
   mounted () {
     
      this.$nextTick( () => {
         window.addEventListener('resize', this.getWindowWidth);
         window.addEventListener('resize', this.getWindowHeight);
         this.getWindowWidth()
         this.getWindowHeight()

         setTimeout(() => {
            this.CheckLoad
         }, 2000);

      })

      this.TrigDispatch()
      
      //FOR VIEW-COACH-COMPONENT
      if (this.GET_CoachSelect === null || this.GET_CoachSelect !== null){
            this.$store.commit('availableCoachDataSelect',{
            CoachIndex : 'undefined'
         })
      }
      this.$store.commit('storeAvailableFilter', null)
      this.$store.commit('StoreFilterBookType', {
         bookType: null,
			booking_type: null,
			frequency: null
      })
   },
	methods: {
      getWindowWidth(event){
         this.windowWidth = document.documentElement.clientWidth;
         console.log(this.ResizeScreen)
         if (this.windowWidth <= 599){
         //this for resolution phone only
         this.ResizeScreen = 1
         }else if(this.windowWidth <= 768){
            //this for resolution Ipad Potrait
         this.ResizeScreen = 2
         }else if(this.windowWidth >= 1024){
            //this for resolution Dekstop-up
         this.ResizeScreen = 3
         }
      },
      getWindowHeight(event){
         this.windowHeigt = document.documentElement.clientHeight;
      },
      TrigDispatch(){
         this.$store.dispatch("POST_AVAILABLECOACH");
         this.$store.dispatch("GET_SELECTFILTER");
      }
   },
   
   computed: {
      LOAD_ViewCoach(){
         return this.$store.getters.BOOK_VIEWCOACH
      },
      GET_CoachResult(){
         return this.$store.state.AvailableCoach
      },
      GET_CoachSelect(){
         return this.$store.state.AvailableCoachSelect
      },
      CheckFilter(){
         return this.$store.state.AvailableFilter
      },
      CheckBookType(){
         return this.$store.state.FilterBookType
      },
      CheckLoadData(){
         return this.$store.state.LoadAvCoach
      },
      CheckLoad(){
         if (this.$store.getters.CCK_BOOK_COACH !== 'IS NULL'){
            const ItemsCont = []
            for (var i in this.$store.getters.CCK_BOOK_COACH){
               ItemsCont.push(this.$store.getters.CCK_BOOK_COACH[i])
            }
            return ItemsCont
         }else{
            return 'IS NULL'
         }
      }
   },

   watch: {
      CheckLoad(val){
         if (val === 'IS NULL'){
            this.TrigDispatch()
            setTimeout(() => {
               if (val === 'IS NULL'){
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Something wrong, Please Reload your Browser'
                  })
                  this.myData = null
                  console.log(val)
               }
            }, 1000);
         
         }else{
            const dataCoachAV = val[0]
            const dataFilterLangCoun = val[1]

            if (dataCoachAV == 200 && dataFilterLangCoun == 200){
               this.myData = 'Success'
               this.$store.commit('MuttAllertCont', {
                  Alert : 'no',
                  TextAlert : 'Success'
               })
            }else if(dataCoachAV == 400 || dataFilterLangCoun == 400){
               this.myData = null
               this.$store.commit('MuttAllertCont', {
                  Alert : 'yes',
                  TextAlert : 'Bad Request, Please try again in a few minutes'
               })
            }else if(dataCoachAV == 500 || dataFilterLangCoun == 500){
               this.myData = null
               this.$store.commit('MuttAllertCont', {
                  Alert : 'yes',
                  TextAlert : 'Server Internal Error, Please try again in a few minutes'
               })
            }else if(dataCoachAV == 401 || dataFilterLangCoun == 401){
               this.myData = null
               this.$store.commit('MuttAllertCont', {
                  Alert : 'yes',
                  TextAlert : 'Unauthorized, Please Logout and Relogin again '
               })
            }else if(dataCoachAV == 404 || dataFilterLangCoun == 404){
               this.myData = 'Success'
               this.$store.commit('MuttAllertCont', {
                  Alert : 'no',
                  TextAlert : 'No found coach'
               })
            }
         }
      }
   },

   beforeDestroy() {
      window.removeEventListener('resize', this.getWindowWidth);
      window.removeEventListener('resize', this.getWindowHeight);
   },
 }

</script>

<style lang="scss">
    @import './BookCoach.scss';
</style>



